.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  height: max-content;
  width: 100%;
  margin-top: 50px;
  text-align: center;
  padding: 20px 0px;
  box-shadow: 6px 6px 14px 0 rgba(0, 0, 0, 0.253), -8px -8px 18px 0 rgba(255, 255, 255, 0.76);
}
.container p{
        margin: 5px;
    }
    .app p {
        margin: 5px;
    }
.social_link a {
  margin: 0 5px 0 5px;
}
